<!-- UI -->
<template>
  <v-container fluid class="p-0">
    <v-card outlined style="background-color: #f7f1e9;">
      <v-row> 
        <v-col cols="4" class="pb-0 pt-0">
          <!-- <p>selectedItems:{{ selectedItems }}</p> -->
          <v-checkbox id="masterCheckbox" @click="[updateTotalPrice(),updateSelectedItems()]" v-model="selectAll" label=" "></v-checkbox>
          
        </v-col>
      </v-row>
   </v-card> 
      <div class="mt-7">
        <v-card v-for="(cartItem, i) in cartItems" :key="i" outlined tile style="background-color: #f7f1e9;">

          <v-row>
            <v-col cols="6" class="pb-0">
              <v-checkbox class="m-0" @click="[updateTotalPrice(),updateSelectedItems()]" v-model="selectedItems" :value="cartItems[i]" :disabled="cartItem.stock==0" :readonly="cartItem.stock==0"> </v-checkbox>
            </v-col>
            <v-col cols="6">
              <button style="float: right" @click="deleteOneItem(cartItem.pstockid)"><v-icon>mdi-close</v-icon></button>
            </v-col>
          </v-row>

          <v-row>
            <v-col cols="3" class="pt-0">
              <v-img class="ml-2" :src="cartItem.img1" width="80" height="90" />
            </v-col>
            <v-col cols="5" class="pt-0">
              <div style="font-weight: bold">{{ cartItem.bname }}</div>
              <div>{{ cartItem.pname }}</div>
              <div>{{ cartItem.occode }} | {{ cartItem.oscode }}</div>
              <button
                @click="quantity_control(cartItem.pstockid, cartItem.quantity, 'minus')"
                style="margin: 0px;"
                type="button"
                class="btn btn-light left1"
              >
              -
              </button>
          <input
            id="quantity"
            name="quantity"
            type="text"
            class="mr0 text-center"
            :value="cartItem.quantity"
            size="1"
            maxlength="2"
            readonly="readonly"
          />
          <button
            @click="quantity_control(cartItem.pstockid, cartItem.quantity, 'plus')"
            style="margin: 0px"
            type="button"
            class="btn btn-light right1"
          >
            +
          </button>
            </v-col>
            <v-col cols="4">
              <div v-if="cartItem.stock < 5 && cartItem.stock > 0"> {{cartItem.stock}} </div>
              <div v-if="cartItem.stock >= 5"> 5 </div>
              <div v-if="cartItem.stock <= 0 "></div>
              <div style="font-weight: bold; font-size: large; color: #959ace;">{{ cartItem.pprice | comma }}</div>
              
            </v-col>
          </v-row>
        <!-- <v-row>
          <v-col cols ="12">
          </v-col>
        </v-row> -->
        </v-card>
      </div>
    
    <v-row class="mt-3">
      <v-col>
        <v-btn v-on:click="deleteOutOfStock" width="100%" outlined  style="background-color: white;">    </v-btn>
      </v-col>
      <v-col>
        <v-btn v-on:click="deleteSelected" width="100%" outlined style="background-color: white;">    </v-btn>
      </v-col>
    </v-row>
    <v-row>
    </v-row>
  </v-container>
</template>

<script>
import cart from "@/apis/member/cart";
import orderform from "@/apis/member/orderform";
import { eventBus } from '../main'
export default {
  //  (devtools  )
  name: "Cart",
  //   
  // components: {},
  //  
  filters: {
    comma(val) {
      return String(val).replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
  },
  data: function () {
    return {
      checkbox: true,
      cartItems: [],
      cartSize: 0,
      selectedItems: [],
      selectedOneItem: null,
      ableToCheckCount: 0
    };
  },

  methods: {
    //     
    updateTotalPrice() {
      let totalPrice = 0;
      this.selectedItems.forEach((item) => {
        totalPrice += item.pprice * item.quantity;
      });
      //    
      eventBus.setTotalPrice(totalPrice);
    },
    updateSelectedItems() {
      eventBus.setToOrderItems(this.selectedItems);
    },
    async quantity_control(pstockid, quantity, operator) {
      let value = quantity;
      //    
      if (operator === "minus") {
        //   1    
        if (value > 1) {
          //   value  
          value = value - 1;
        }
        //    
      } else if (operator === "plus") {
        //  
        value = value + 1;
      }
      //       DB .
      if (value !== quantity) {
        let map = {
          pstockid: pstockid,
          quantity: value,
        };

        await cart.updateQuantity(map);
        // 
        this.$router.go();
      }
    },
    async toOrderForm() {
      let response = await orderform.getMemberInfo();
      let initMemberInfo = response.data;
      let toOrderItemList = [];
      this.selectedItems.forEach((item) => {
        let toOrderItem = {
          pstockid: item.pstockid,
          img1: item.img1,
          pprice: Number(item.pprice),
          scode: item.oscode,
          ccode: item.occode,
          bname: item.bname,
          pname: item.pname,
          quantity: Number(item.quantity),
          appliedPrice: Number(item.pprice) * Number(item.quantity),
        };
        toOrderItemList.push(toOrderItem);
      });
      this.$router.push({
        name: "orderform",
        params: {
          initCartItems: toOrderItemList,
          initMemberInfo: initMemberInfo,
        },
      });
    },
    //    
    async deleteOutOfStock() {
      let delItems = [];
      this.cartItems.forEach((item) => {
        if (item.stock <= 0) delItems.push(item.pstockid);
      });
      //       
      if (delItems.length == 0) return;
      else {
        await cart.deleteSelected(delItems);
        this.$router.go();
      }
    },

    //     
    async deleteSelected() {
      let delItems = [];
      this.selectedItems.forEach((item) => {
        delItems.push(item.pstockid);
      });
      const response = await cart.deleteSelected(delItems);
      // 
      this.$router.go();
    },
    async deleteOneItem(pstockid) {
      console.log("pstockid =", pstockid);
      await cart.deleteOneItem(pstockid);
      this.$router.go();
    },
  },
  beforeCreate() {
    cart.cartItems().then((response) => {
      let cartItems = response.data;
      this.cartItems = cartItems;
      let ableToCheckCount = 0;
      cartItems.forEach((item) => {
        if(item.stock > 0) {
          ableToCheckCount++;
        }
      });
      //    
      this.ableToCheckCount = ableToCheckCount;
      this.cartSize = response.data.length;
      console.log("response.data =", response.data);
      console.log("response.data.length =", this.cartSize);
    });
  },
  computed: {
    selectAll: {
      //      true   false 
      get: function () {
        // if(this.selectedItems.length == this.ableToCheckCount) return true;
        // else return false;
        return this.selectedItems ? this.selectedItems.length == this.ableToCheckCount : false;
      },

      set: function (value) {
        // tmp
        var selectedItems = [];

        //    
        if (value) {
          this.cartItems.forEach(function (cartItem) {
            if(cartItem.stock>0) {
              selectedItems.push(cartItem);
            }
          });
        }
        //       ,      .
        this.selectedItems = selectedItems;
      },
    },
    // selectCount: {
    //   get: function () {
    //     return this.selectedItems.length;
    //   },
    // },
  },
};
</script>

<!--  -->
<!--scoped   -->
<style>
.v-application .primary--text {
    color: #000051 !important;
    caret-color: #000051 !important;
}
</style>
