import axios from "axios";

const instance = axios.create({
  baseURL: 'http://localhost:81'
  //baseURL:'http://kosa1.iptime.org:50320'
});

/*    session token     */
instance.interceptors.request.use(function (config) {
  const token = sessionStorage.getItem('authToken');
  config.headers.Authorization =  token ? `Bearer ${token}` : '';
  return config;
});
/*    session token     */

function insertCartItem(pstockid, quantity) {
  console.log("insertCartItem");
  console.log(pstockid+" "+quantity);
  return instance.post("/cart",{
    mid: null,
    pstockid: pstockid,
    quantity: quantity,
  });
}

function cartItems() {
  return instance.get("/cart");
}

function deleteSelected(delItems) {
  return instance.post(
    "/cart/deleteSelected",
    delItems
    );
}

function deleteOneItem(pstockid) {
  console.log("pstockid   = ", pstockid);
 return instance.post("/cart/delete", 
      pstockid, 
      {headers: {"Content-Type": "text/plain"}} //  json    
      );
}

function deleteByOrder(delItemsByOrder) {
  return instance.post("/cart/deletebyorder", delItemsByOrder);
}

function updateQuantity(map) {
  return instance.post("/cart/update/quantity", map);
}

export default {
  cartItems,
  deleteSelected,
  deleteOneItem,
  insertCartItem,
  deleteByOrder,
  updateQuantity
}